.question-header{display:flex;align-items:center;gap:20px;font-size:28px;font-family:"Cofo Bold"}.question-header-number{font-size:48px}.question .description{margin-top:10px;font-size:18px;color:#656565;max-width:265px}.question .answers{margin-top:35px;margin-left:20px}.question .answers button{border:none;background:rgba(0,0,0,0);font-size:18px;width:85%;margin-left:10%;text-align:left}.question .answers button.wrong-btn{color:#ff6262}.question .answers button.correct-btn{color:#269d75}.question .answers ol{display:flex;flex-direction:column;gap:9px;list-style:none}.question .answers ol li{position:relative}.question .answers ol li:before{position:absolute;left:-5px;top:50%;font-family:"Cofo Italic";font-size:20px;transform:translateY(-50%)}.question .answers ol li:nth-child(1)::before{content:"а"}.question .answers ol li:nth-child(2)::before{content:"б"}.question .answers ol li:nth-child(3)::before{content:"в"}