@font-face {
    font-family: 'Cofo Regular';
    src: url('./assets/fonts/CoFoSans-Regular.woff') format('woff');
}
@font-face {
    font-family: 'Cofo Italic';
    src: url('./assets/fonts/CoFoRobert-RegularItalic.woff') format('woff');
}
@font-face {
    font-family: 'Cofo Black';
    src: url('./assets/fonts/CoFoSans-Black.woff') format('woff');
}
@font-face {
    font-family: 'Cofo Bold';
    src: url('./assets/fonts/CoFoSans-Bold.woff') format('woff');
}
@font-face {
    font-family: 'Cofo Medium';
    src: url('./assets/fonts/CoFoSans-Medium.woff') format('woff');
}
* {
    font-family: 'Cofo Regular';
    margin: 0;
    padding: 0;
}
body {
    background-color: white;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu',
        'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    height: calc(100lvh - 40px);
    margin: 0 auto;
    padding: 20px 12px;
}
html,
body {
    overflow: hidden;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

#root {
    height: inherit;
}
.App {
    height: inherit;
}
.box {
    width: 421px;
    height: 445px;
    transform: rotate(13.027deg);
    flex-shrink: 0;
    display: block;
    content: '';
    position: absolute;
    z-index: -1;
    animation: hover 4s infinite ease-in-out;
}
.blue-box {
    animation-delay: 1s;
    background: #bdddf4;
    right: -200px;
    top: -200px;
}
.pink-box {
    background: #fec0c3;
    bottom: -200px;
    left: -200px;
}

@media screen and (max-width: 400px) {
    .blue-box {
        right: -300px;
        top: -200px;
    }
    .pink-box {
        bottom: -200px;
        left: -300px;
    }
}

button.link {
    border: none;
    background: transparent;
    font-family: 'Cofo Bold';
    font-size: 18px;
    color: #68bcf9;
    display: block;
    margin-bottom: 20px;
    margin: 10px auto;
}
button.link::after {
    background-color: #68bcf9;
    height: 2px;
    width: 100%;
    display: block;
    content: '';
}

@keyframes showToUp {
    from {
        transform: translateY(300px);
    }
    to {
        transform: translateY(0px);
    }
}

@keyframes hover {
    0% {
        transform: translateY(0) rotate(13.027deg);
    }
    25% {
        transform: translateY(-6px) rotate(11.027deg);
    }
    75% {
        transform: translateY(6px) rotate(14.027deg);
    }
    100% {
        transform: translateY(0) rotate(13.027deg);
    }
}
